import Vue from 'vue'
import App from './App'
import router from './router'
import store from '@/store/index'
import firebase from 'firebase'

Vue.config.productionTip = false

// Move it later to specific configuration file
// Initialize Firebase
const config = {
  apiKey: 'AIzaSyACjBLuXgLCbiZo6GtfYeScEwYDRl2j0ow',
  authDomain: 'rpg-base.firebaseapp.com',
  databaseURL: 'https://rpg-base.firebaseio.com',
  projectId: 'rpg-base',
  storageBucket: 'rpg-base.appspot.com',
  messagingSenderId: '16628175290'
}

firebase.initializeApp(config)

/* eslint-disable no-new */
new Vue({
  el: '#app',
  store,
  firebase,
  router,
  components: { App },
  template: '<App/>'
})



