import Vue from 'vue'
import Router from 'vue-router'
import Greetings from '@/components/Greetings'
import Login from '@/components/Login'
import SignUp from '@/components/SignUp'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/greetings',
      name: 'Greetings',
      component: Greetings
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/signup',
      name: 'SignUp',
      component: SignUp
    }
  ]
})



